import React from 'react';

function Image(props) {
  const {
    ice,
    model: { image_s, alternativeText_s }
  } = props;
  return (
    <>
      <img {...ice} src={image_s} alt={alternativeText_s || ''} className="img-fluid" />
    </>
  );
}

export default Image;
